@import "variables";

ul.pagination {
    margin-top: 20px;
    margin-bottom: 20px;

    @media (min-width: 768px) {
        transform: translateX(-5px);
    }

    li {
        display: inline-block;
        margin: 5px;

        a {
            text-transform: capitalize;
            padding: 5px 12px;
            border: 1px solid $dark;
            color: $dark;
            border-radius: 3px;
            user-select: none;

            &:hover {
                color: $white;
                border-color: $primary;
                background-color: $primary;
            }
        }

        &.disabled {
            opacity: 0.4;
        }

        &.active {
            a {
                color: $white;
                border-color: $primary;
                background-color: $primary;
            }
        }
    }
}

.tracks-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-left: -15px;
    margin-right: -15px;
}

.track {
    flex-basis: 100%;
    max-width: 100%;
    padding: 15px;

    @media (min-width: 426px) {
        flex-basis: 50%;
        max-width: 50%;
    }

    @media (min-width: 768px) {
        flex-basis: 33.333%;
        max-width: 33.333%;
    }
    
    @media (min-width: 991px) {
        flex-basis: 25%;
        max-width: 25%;
    }
    
    @media (min-width: 1200px) {
        flex-basis: 20%;
        max-width: 20%;
    }

    &__name {
        margin: 10px 0 16px;
        text-transform: capitalize;
        font-size: 20px;
    }

    &__artist-link {
        margin: 0 0 16px;
    }

    &__last-fm-link {
        margin: 0 0 10px;
    }
}

.search-form {
    input {
        max-width: 320px;
        width: 100%;
        padding: 10px;
        border: 1px solid $dark;
    }
    button {
        transition: 0.3s all ease;
        cursor: pointer;
        padding: 10px;
        border: 1px solid $dark;
        color: $dark;
        background-color: transparent;

        &:hover {
            color: $white;
            background-color: $primary;
            border-color: $primary;
        }
    }

    > * {
        margin-top: 14px;
    }
}